'use client'

import React, { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Col, Container, Form, Row, Button, Spinner } from "react-bootstrap";
import { userForgotPassword } from '@/redux/actions/userAuthAction';


export default function page() {
    const mssg = useSelector((state) => state.userRdcr.mssg)
    const dispatch = useDispatch()
    const[data, setData] = useState({
        email: ""
    })

    const [loading, setLoading] = useState(false)


    const handleInputChange = (e) => {
        setData({
          ...data,
          [e.target.name]: e.target.value,
        });
      };
      
      
      
        const handleSubmit = async(event) => {
      
          event.preventDefault()
              setLoading(true);
              try {
                
                await dispatch(userForgotPassword(data))
      
              } finally {
                setLoading(false);
              }
          
          
      
        }
      


   
  return (
    <>

<Container style={{marginTop: "2rem"}}>

<div className="text-center text-danger" style={{marginTop: "15px"}}>


{mssg && ( <h3>{mssg}</h3>)}

</div>



    <Row className="justify-content-md-center">

    <Col xs={12} md={6}>
  <h3 className='text-center'>Write your email to reset your password!</h3>

  <Form onSubmit={handleSubmit}>
    <Form.Group className="mb-3" controlId="formBasicEmail">
      <Form.Label>Email</Form.Label>
      <Form.Control
        type="text"
        name="email"
            value={data.email}
            onChange={handleInputChange}
            
        
        placeholder="write email"
      />
    </Form.Group>

    
    <Button variant="danger" type="submit" disabled={loading}>
                {loading ? (
                  <>
                    <Spinner as="span" animation="border" size="sm" role="status" aria-hidden="true" />
                    <span className="ms-2">Loading...</span>
                  </>
                ) : (
                  "Submit"
                )}
              </Button>


  </Form>
  
</Col>
</Row>   
</Container>


    
    
    </>
  )
}
